import axios from "axios";
import { useEffect, useState } from "react";

const Notes = ({ match }) => {
  const [data, setData] = useState({});
  const [user, setUser] = useState({});
  const [date, setDate] = useState(null);
  const [token, setToken] = useState(window.localStorage.getItem("token"));

  const get_notes = () => {
    axios
      .get(`http://localhost:8080/api/notes/${match.params.id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => setData(res.data.notes))
      .catch((err) => console.log(err));
    axios
      .get(`http://localhost:8080/api/users/me`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => setUser(res.data.user))
      .catch((err) => console.log(err));
  };
  const { text, userId, completed, createdDate } = data;
  useEffect(() => {
    get_notes();
    setTimeout(() => {}, 3000);
  }, []);

  return (
    <div className="container">
      <div className="card col-6 offset-3 mt-5">
        <div className="card-header"></div>
        <div className="card-body">
          <h3>Content:</h3>
          <h4>{text}</h4>
          <h5>{completed ? "Completed" : "Uncompleted"}</h5>
        </div>
        <div className="card-footer">
          <h5>
            Created in: <strong>{createdDate}</strong>
          </h5>
          <h5>
            Creator: <strong>{user[0] && user[0].username}</strong>
          </h5>
        </div>
      </div>
    </div>
  );
};

export default Notes;
