import axios from "axios";
import { useEffect, useRef, useState } from "react";
import Todo from "../Todo";
import Loader from "../Loader/Loader.jsx";
const Form = () => {
  const [data, setData] = useState({});
  const [token, setToken] = useState(window.localStorage.getItem("token"));
  const ref = useRef();
  const handleCreate = () => {
    axios
      .post(
        "http://localhost:8080/api/notes",
        { text: ref.current.value },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            "Access-Control-Allow-Origin": "*",
          },
        }
      )
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
    ref.current.value = "";
    get_notes();
  };
  const get_notes = () => {
    axios
      .get("http://localhost:8080/api/notes", {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => setData(res.data.notes))
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    get_notes();
  }, []);
  return (
    <div className="container">
      {token ? (
        <div className="card col-6 offset-3 mt-5">
          <div className="card-header d-flex">
            <input
              placeholder="Create notes..."
              ref={ref}
              type="text"
              className="form-control"
            />
            <button
              onClick={handleCreate}
              className="ms-1 btn-outline-primary btn"
            >
              Create
            </button>
          </div>
          <div className="card-body">
            {data.length > 0 ? (
              <Todo.Group>
                {data.map((el) => (
                  <Todo.Option
                    token={token}
                    id={el._id}
                    checked={el.completed}
                    key={el._id}
                    title={el.text}
                  />
                ))}
              </Todo.Group>
            ) : (
              <Loader />
            )}
          </div>

          <div className="card-footer">
            <button className="btn btn-danger">Delete</button>
          </div>
        </div>
      ) : (
        <div class="alert alert-danger mt-5 p-5" role="alert">
          <h1>Create a todo please Sign in first</h1>
        </div>
      )}
    </div>
  );
};

export default Form;
