import axios from "axios";
import { useRef, useState } from "react";
import {Link} from 'react-router-dom'
import { MdDelete, MdEdit } from "react-icons/md";
import { BsFillInfoCircleFill } from "react-icons/bs";

const Option = ({ title, checked, id, token }) => {
  const ref = useRef();
  const onChange = () => {
    axios
      .patch(`http://localhost:8080/api/notes/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => alert(res))
      .catch((err) => console.log(err));
  };
  const [data, setData] = useState(title);

  const handleChange = () => {};
  const handleDelete = (id) => {
    axios
      .delete(`http://localhost:8080/api/notes/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
      })
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };
  const handleSave = () => {
    ref.current.disabled = true;
    // request for changed note
    axios
      .put(
        `http://localhost:8080/api/notes/${id}`,
        { text: data },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };
  const handleEdit = () => {
    ref.current.disabled = false;
    ref.current.focus();
  };
  return (
    <li className="list-group-item">
      <label
        class="w-100 float-nonecustom-control-label  d-flex align-items-center justify-content-between"
        htmlFor={title}
      >
        <div
          onClick={onChange}
          class=" w-100 custom-control custom-checkbox d-flex align-items-center"
        >
          <input
            checked={checked}
            type="checkbox"
            class="custom-control-input"
            id={id}
          />
          <input
            onBlur={handleSave}
            onClick={handleChange}
            onChange={(e) => {
              setData(e.target.value);
            }}
            disabled
            ref={ref}
            className="border-0 ms-2 w-100 form-control"
            type="text"
            value={data}
          />
        </div>
        <button
          className="btn btn-primary float-end ms-2 d-flex align-items-center justify-content-center"
          onClick={handleEdit}
        >
          <MdEdit />
        </button>
        <button
          onClick={() => handleDelete(id)}
          className="btn btn-danger d-flex align-items-center justify-content-center ms-1"
        >
          <MdDelete />
        </button>
        <button
        
          className="btn btn-warning d-flex align-items-center justify-content-center ms-1"
         >
         <Link to={`/${id}`}>
          <BsFillInfoCircleFill style={{fill:'white'}}/>
         </Link>
        </button>
      </label>
    </li>
  );
};

export default Option;
