import { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
const Auth = () => {
  const history = useHistory();
  let [account, setAccount] = useState(true);
  let [data, setData] = useState({});
  const handleChange = (e) => {
    setData({ ...data, [e.name]: e.value });
  };
  const handleSubmit = () => {
    const endPoint = `http://localhost:8080/${
      account ? "api/auth/register" : "api/auth/login"
    }`;
    const headers = {
      "Access-Control-Allow-Origin": "*",
      "Content-type": "application/json",
    };
    axios
      .post(endPoint, {...data}, { headers })
      .then((res) => window.localStorage.setItem("token", res.data.jwt_token))
      .then(history.push("/"))
      .catch((error) => console.error(error));
    // window.location.reload();
  };
  return (
    <div className="container">
      {account ? (
        <Register
          setAccount={setAccount}
          handleChange={handleChange}
          handleSubmit={handleSubmit}
        />
      ) : (
        <Login
          setAccount={setAccount}
          handleSubmit={handleSubmit}
          handleChange={handleChange}
        />
      )}
    </div>
  );
};
const Register = ({ setAccount, handleChange, handleSubmit }) => {
  return (
    <div className=" col-6 mt-5 offset-2">
      <h1 className="py-5">Registration</h1>
      <div className="mb-3">
        <label for="exampleInputEmail1" className="form-label">
          Email address
        </label>
        <input
          type="text"
          onChange={(e) => handleChange(e.target)}
          name="username"
          className="form-control"
          id="exampleInputEmail1"
        />
      </div>
      <div className="mb-3">
        <label for="exampleInputPassword1" className="form-label">
          Password
        </label>
        <input
          type="password"
          onChange={(e) => handleChange(e.target)}
          className="form-control"
          name="password"
        />
      </div>
      <button onClick={handleSubmit} type="submit" className="btn btn-primary">
        Register
      </button>
      <button
        onClick={() => setAccount(false)}
        className="btn text-decoration-underline d-block mt-5"
      >
        Already created an account?
      </button>
    </div>
  );
};

const Login = ({ setAccount, handleSubmit }) => {
  return (
    <div className=" col-6 mt-5 offset-2">
      <h1 className="py-5">Login</h1>
      <div className="mb-3">
        <label for="mail" className="form-label">
          Email address
        </label>
        <input type="email" className="form-control" id="mail" />
      </div>
      <div className="mb-3">
        <label for="exampleInputPassword1" className="form-label">
          Password
        </label>
        <input
          type="password"
          className="form-control"
          id="exampleInputPassword1"
        />
      </div>
      <button onClick={handleSubmit} className="btn btn-primary">
        Login
      </button>
      <button
        onClick={() => setAccount(true)}
        className="btn text-decoration-underline d-block mt-5"
      >
        No account? Let's create a new one.
      </button>
    </div>
  );
};

export default Auth;
