import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { useHistory } from "react-router";
const Profile = () => {
  const history = useHistory();
  const handleLogOut = () => {
    window.localStorage.removeItem("token");
    history.push("/auth");
  };
  const handleDeleteAccount = () => {
    axios
      .delete(`http://localhost:8080/api/users/me`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => alert(res.data.message))
      .catch((err) => console.log(err));
    window.localStorage.removeItem("token");
    history.push("/auth");
  };
  const [user, setUser] = useState({});
  const oldRef = useRef(null);
  const newRef = useRef(null);
  const [token, setToken] = useState(window.localStorage.getItem("token"));
  const handleSubmit = (e) => {
    e.preventDefault();
    axios
      .patch(
        `http://localhost:8080/api/users/me`,
        {
          oldPassword: oldRef.current.value,
          newPassword: newRef.current.value,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            "Access-Control-Allow-Origin": "*",
          },
        }
      )
      .then((res) => alert(res.data.message))
      .catch((err) => console.log(err));
  };
  const get_notes = () => {
    axios
      .get(`http://localhost:8080/api/users/me`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => setUser(res.data.user))
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    get_notes();
  }, []);
  return (
    <div className="container">
      <div className="card mt-5">
        <div className="card-header">
          Credentials of <strong>{user[0] && user[0].username}</strong>
        </div>
        <div className="card-body">
          <button
            onClick={handleDeleteAccount}
            className="btn mx-2 btn-danger btn-lg"
          >
            Delete account
          </button>
          <button onClick={handleLogOut} className="btn btn-danger btn-lg">
            Log out
          </button>
        </div>
        <div className="card-footer">
          <form
            onSubmit={(e) => handleSubmit(e)}
            className="form-inline d-flex align-items-center"
          >
            <div className="form-group mx-sm-3 mb-2">
              <label for="inputPassword2" className="sr-only">
                Old password
              </label>
              <input
                type="password"
                ref={oldRef}
                className="form-control"
                id="inputPassword2"
                placeholder="Password"
              />
            </div>
            <div className="form-group mx-sm-3 mb-2">
              <label for="inputPassword2" className="sr-only">
                New password
              </label>
              <input
                type="password"
                className="form-control"
                ref={newRef}
                id="inputPassword2"
                placeholder="Password"
              />
            </div>
            <button type="submit" className="btn btn-primary mb-2">
              Change
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Profile;
